"use strict";

import hsl from "../helpers/hsl";

const size = function () {
	return {
		width:  (window.innerWidth || document.documentElement.clientWidth),
		height: (window.innerHeight || document.documentElement.clientHeight),
	}
};

let scene;
let mesh;
let camera;

const renderer = new THREE.WebGLRenderer( {
	alpha: true,
// 	antialias: true
} );
// renderer.shadowMap.enabled = true;

document.body.appendChild( renderer.domElement );

const resize = function () {
	let s = size();

	if ( !!renderer ) {
		renderer.setSize( s.width, s.height );
	}

	if ( !!camera ) {
		camera.aspect = s.width / s.height;
		camera.updateProjectionMatrix();
	}
};

window.addEventListener( 'resize', resize, false );

export const Display = {
	init: function () {
		// Scene

		scene = new THREE.Scene();

		// Mesh

		mesh = new THREE.Object3D();
		scene.add( mesh );

		// Camera

		let s = size();

		const aspectRatio = s.width / s.height;
		const fieldOfView = 60;
		const nearPlane   = 1;
		const farPlane    = 10000;
		camera            = new THREE.PerspectiveCamera(
			fieldOfView,
			aspectRatio,
			nearPlane,
			farPlane
		);

		camera.position.x = 0;
		camera.position.z = 200;
		camera.position.y = 0;

		// Lights

		let shadowLight = new THREE.DirectionalLight( 0xffffff, .9 );
		shadowLight.position.set( 0, 0, 100 );
		shadowLight.castShadow = false;
		scene.add( shadowLight );

		// Resize

		resize();
	},

	addVoxel: function ( x, y, z, color ) {
		let rgb = hsl( color, 75, 50 );

		let geometry = new THREE.BoxGeometry( 2 * x, 2 * y, 2 * z, 1, 1, 1 );
		let material = new THREE.MeshPhongMaterial( { color: ((rgb[ 0 ] << 2) + (rgb[ 1 ] << 1) + (rgb[ 2 ])), shading: THREE.FlatShading } );
		let cube     = new THREE.Mesh( geometry, material );
// 		cube.castShadow    = true;
// 		cube.receiveShadow = true;
		mesh.add( cube );
	},

	render: function () {
		renderer.render( scene, camera );
	}
};


